import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import {
  Form,
  Input,
  Radio,
  InputNumber,
  Button,
  Card,
  Layout,
  Menu,
  Divider,
} from "antd";

const { Header, Content, Footer } = Layout;

const url = "https://662d-27-55-93-160.ngrok.io/";
const postUrlCalculate = url + "water/area/calculate";

const menuItems = ["แรงบันดาลใจ", "ข้อจำกัด", "ข้อดี", "บริการของเรา", "คลังความรู้", "ติดต่อ/ข้อเสนอแนะ"].map((key) => ({
  key,
  label: `${key}`,
}));
const tabList = [
  {
    key: "อุปโภค - บริโภค",
    tab: "อุปโภค - บริโภค",
  },
  {
    key: "เพาะปลูก",
    tab: "เพาะปลูก",
  },
  {
    key: "ปศุสัตว์",
    tab: "ปศุสัตว์",
  },
  {
    key: "อุตสาหกรรม",
    tab: "อุตสาหกรรม",
  },
];


const FormDemand = () => {

  const [area, setArea] = useState(null);
  const [numPeople, setNumPeople] = useState(null);
  const [waterDemand, setWaterDemand] = useState(null);
  const [result, setResult] = useState(null);
  const [postResponseFormulaCalculate, setPostResponseFormulaCalculate] = useState(null);

  const onChangeRadioArea = (e) => {
    console.log("radio checked", e.target.value);
    setArea(e.target.value);
  };

  const onChangeNumPeople = (e) => {
    console.log("num people changed", e.target.value);
    setNumPeople(e.target.value);
  };

  const onChangeWaterDemand = (e) => {
    console.log("water demand changed", e.target.value);
    setWaterDemand(e.target.value);
  };

  const onClickCalculate = async (e) => {
    await axios.post(postUrlCalculate, {
      "area_name": area,
      "number_person": numPeople,
      "number_day" : waterDemand
    })
      .then((response) => {
        setPostResponseFormulaCalculate(response.data)
        setResult(response.data.data.result)
      });
  };

  postResponseFormulaCalculate != null ? console.log("-", result) : console.log("-null-");

  return (
    <>
      <Header style={{ backgroundColor: "#1890FF" }}>
        <h1
          style={{
            float: "left",
            width: "20%",
            color: "white",
          }}>
          FARM DESIGN
        </h1>
        <Menu
          theme="dark"
          mode="horizontal"
          defaultSelectedKeys={["0"]}
          items={menuItems}
          style={{
            float: "right",
            width: "50%",
            backgroundColor: "#1890FF"
          }}
        />
      </Header>
      <Content
        style={{
          padding: "2% 10%",
        }}
      >
        <h1 style={{ textAlign: "start" }}>คำนวณความต้องการน้ำ</h1>
        <Card
          tabList={tabList}
          bordered={true}
          headStyle={{}}
          bodyStyle={{ textAlign: "start", boxShadow: "2px 2px 2px 2px lightgray" }}
        >
          <Form
            labelCol={{
              span: 3,
            }}
            wrapperCol={{
              span: 24,
            }}
            layout="horizontal"
            labelAlign={"left"}
            style={{
              fontWeight: "bold",
            }}
          >
            <Form.Item label="เขตพื้นที่">
              <Radio.Group onChange={onChangeRadioArea} value={area}>
                <Radio value="กรุงเทพฯ"> กรุงเทพฯ </Radio>
                <Radio value="องการบริหารส่วนตำบล"> องการบริหารส่วนตำบล </Radio>
                <Radio value="เทศบาลตำบล"> เทศบาลตำบล </Radio>
                <Radio value="เทศบาลนคร"> เทศบาลนคร </Radio>
                <Radio value="เมืองพัทยา"> เมืองพัทยา </Radio>
                <Radio value="เทศบาลเมือง"> เทศบาลเมือง </Radio>
              </Radio.Group>
            </Form.Item>
            <Form.Item label="จำนวนผู้ใช้น้ำ" onChange={onChangeNumPeople}>
              <InputNumber style={{ width: "20%" }} /> คน
            </Form.Item>
            <Form.Item label="ความต้องการน้ำ" onChange={onChangeWaterDemand}>
              <Input style={{ width: "20%" }} /> วัน
            </Form.Item>
            <Divider />
            <Form.Item label="ความต้องการน้ำทั้งหมด">
              <Input
                disabled style={{
                  width: "20%",
                  fontWeight: "bold",
                  color: "#1890FF",
                }}
                value={result}
              /> ลูกบาศก์เมตร
              <Button
                type="primary"
                onClick={onClickCalculate}
                style={{
                  float: "right",
                  width: "20%",
                  fontWeight: "bold"
                }}
              >
                คำนวณ
              </Button>
            </Form.Item>
          </Form>
        </Card>
      </Content>
      <Footer
        style={{
          textAlign: 'center',
        }}
      >
        Copyright ©2022 Thai Water Farm. All rights reserved.
      </Footer>
    </>
  );
};

export default () => <FormDemand />;